* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.hero-page {
  background: linear-gradient(120deg, var(--blueGradient), var(--pinkGradient));
}

.people-affected {
  background: linear-gradient(45deg, var(--blueGradient), var(--pinkGradient));
}

.incluision-power {
  background: linear-gradient(120deg, var(--blueGradient), var(--pinkGradient));
}

.solution {
  background: linear-gradient(45deg, var(--blueGradient), var(--pinkGradient));
}

.solution-part2 {
  background: linear-gradient(120deg, var(--blueGradient), var(--pinkGradient));
}

.market {
  background: linear-gradient(45deg, var(--blueGradient), var(--pinkGradient));
}

.prizes {
  background: linear-gradient(120deg, var(--blueGradient), var(--pinkGradient));
}

.news {
  background: linear-gradient(45deg, var(--blueGradient), var(--pinkGradient));
}

.team {
  background: linear-gradient(120deg, var(--blueGradient), var(--pinkGradient));
}

.footer {
  height: 50dvh;
}

.hero-page img {
  height: 600px;
}

.hero-page .logo .subtitle p {
  background: linear-gradient(rgba(45, 44, 45, 0.15), rgba(202, 214, 252, 0.01));
  background-clip: text;
  -webkit-background-clip: text; /* Alguns navegadores precisam do prefixo */

  /* Terceiro passo: apagar o texto, deixando apenas o fundo atr√°s dele */
  -webkit-text-fill-color: transparent;
}

.hero-page .container-info {
  margin-top: -170px;
}

.incluision-power .general-data {
  background: linear-gradient(120deg, #F8A4DC, #F8B1AF);
}

.incluision-power .general-data .data .general-data-info {
  background: rgba(255, 255, 255, 0.25 );
  box-shadow: 2px 2px 16px 0 rgba(58, 58, 58, 0.05);
  backdrop-filter: blur(6.5px);
  -webkit-backdrop-filter: blur(6.5px);
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.18 );
}

.incluision-power .language-data img {
  height: 300px;
  width: auto;
  position: relative;
}

.incluision-power .language-data {
  margin-top: -75px;
}

.solution .economy img {
  height: 250px;
  width: 300px;
  -webkit-transform: scaleX(-1);
  transform: scaleX(-1);
}

.market .cards-info .card {
  background: linear-gradient(120deg, #F8A4DC, #F8B1AF);
}

.prizes img {
  height: 200px;
  width: 300px;
  margin-top: -150px;
}

.news .news-sp .news-info a {
  width: 160px;
  height: 48px;
}

.team .photos .background {
  background: radial-gradient(#595757, #2F2F2F);
}

.team .photos img {
  margin-top: -60px;
}

@media screen and (max-width: 800px) {
  body {
    overflow-x: hidden;
  }

  .hero-page {
    display: flex;
    flex-direction: column;
    width: 100dvw;
    height: 100dvh;
  }

  .hero-page .container-info {
    display: flex;
    flex-direction: column;
  }

  .hero-page .container-info .hero-page-title {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    text-align: center;
    margin-top: 0;
    padding-top: 0;
  }

  .hero-page .container-info .hero-page-title h2 {
    font-size: 30px;
  }

  .hero-page .container-info img {
    height: 300px;
    width: 250px;
  }

  .hero-page .container-info .hero-page-subtitle {
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
  }

  .hero-page .logo .subtitle p {
    font-size: 20px;
  }

  .incluision-power .general-data .data {
    display: flex;
    flex-direction: column;
  }

  .market .cards-info {
    display: flex;
    flex-direction: column;
  }

  .market .cards-info .card {
    width: 200px;
    height: 500px;
  }
}